@import url(https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Syne&display=swap);
:root {
	--tooltip-background: #f5f5f9;
	--tooltip-foreground: rgba(0, 0, 0, 0.87);
	--tooltip-borders: #dadde9;
	--foreground-color: teal;
	--date-color: rgb(0, 170, 170);
	--light-blue: #03a9f4;
	--light-blue-50: #e1f5fe;
	--light-blue-100: #b3e5fc;
	--light-blue-200: #81d4fa;
	--light-blue-300: #4fc3f7;
	--light-blue-400: #29b6f6;
	--light-blue-500: #03a9f4;
	--light-blue-600: #039be5;
	--light-blue-700: #0288d1;
	--light-blue-800: #0277bd;
	--light-blue-900: #01579b;
}

* {
	font-family: 'Syne', sans-serif;
}

body {
	margin: 0;
	box-sizing: border-box;
	color: teal;
	color: var(--foreground-color);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

text {
	margin: 0;
	padding: 0;
}

::selection {
	background-color: teal;
	background-color: var(--foreground-color);
	color: white;
}

::-webkit-input-placeholder {
	color: teal;
}

:-ms-input-placeholder {
	color: teal;
}

::-ms-input-placeholder {
	color: teal;
}

::placeholder {
	color: teal;
}

/* scroll bar */

::-webkit-scrollbar {
	width: 10px;
	z-index: 100;
}

::-webkit-scrollbar-track {
	background-color: #e1f5fe;
	background-color: var(--light-blue-50);
	border-radius: 10px;
}

::-webkit-scrollbar-thumb {
	background-color: rgb(0, 216, 216);
	border-radius: 10px;
}
::-webkit-scrollbar-thumb:active {
	background-color: rgb(0, 179, 179);
}

.inputs * {
	color: teal;
}
.intro {
	display: flex;
}
.intro > * {
	border-radius: 5px;
	border: 1px solid teal;
	background-color: white;
}
.intro > *:focus {
	border-radius: 5px;
	border: 1px solid var(--light-blue-500);
	background-color: var(--light-blue-50);
}
.url {
	flex: 1 1 auto;
	height: auto;
	margin: 0 5px;
	padding: 10px;
	resize: none;
}

.methods {
	width: 90px;
	display: inline-block;
	cursor: pointer;
	padding-left: 10px;
}
.send {
	cursor: pointer;
	padding: 10px 15px;
}
.send:disabled {
	cursor: not-allowed;
}
.send:hover {
	border-color: var(--light-blue-500);
}

header h1 {
	text-transform: uppercase;
}

.log_tooltip * {
	margin: 0 0 10px 0;
}
.log_tooltip h4 {
	margin-top: 0;
}
.log_data {
	max-height: 200px;
	overflow: auto;
}

.logs {
	width: 200px;
	min-width: 200px;
	flex: 1 1 auto;
	margin-left: 10px;
}
.logs_body {
	padding: 10px 5px;
	border: 1px solid teal;
	border-radius: 5px;
}
.logs_body h3 {
	margin: 0;
}

.log {
	display: flex;
	align-items: center;
	flex-direction: column;
	border-bottom: 1px solid teal;
	margin-bottom: 10px;
	padding-bottom: 10px;
	cursor: default;
	border-radius: 5px;
}
.log:hover {
	background-color: var(--light-blue-50);
}
.log:last-of-type {
	border: none;
	margin: 0 0 5px 0;
}

.log_url {
	margin: 0;
	padding: 0;
	display: block;
	width: 100%;
	word-wrap: break-word;
}
.log_timestamp {
	color: var(--date-color);
}
.log_clear {
	padding: 5px 10px;
	background-color: white;
	border: 1px solid teal;
	border-radius: 5px;
	cursor: pointer;
}
.log_clear:hover {
	border-color: var(--light-blue-500);
}

footer {
	margin: 20px 0;
}

.app {
	margin: 0 10vw;
	text-align: center;
}
h1,
h2 {
	font-family: 'Arvo', serif;
}
.app * {
	outline: none;
}
@media screen and (max-width: 992px) {
	.app {
		margin: 0 6vw;
	}
}
@media screen and (max-width: 568px) {
	.app {
		margin: 0 4vw;
	}
}
.row {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.inputs {
	flex: 5 1;
	min-width: 400px;
}
.input_element {
	margin: 10px 0 0 0;
	display: flex;
	flex-direction: column;
}
.input_element label {
	text-align: start;
	padding: 15px 0 15px 15px;
	background-color: var(--light-blue-50);
	border: 1px solid teal;
	border-bottom: none;
	border-radius: 5px 5px 0 0;
}
.input_element textarea {
	resize: none;
	padding: 15px;
	border: 1px solid teal;
	border-radius: 0 0 5px 5px;
}
.input_element textarea:focus {
	background-color: var(--light-blue-50);
}

